.App {
  max-width: 460px;
  margin: 0px auto;
}

button {
  background: red;
  border: 2px solid white;
  padding: 6px 12px;
  border-radius: 4px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}

.flex {
  display: flex;
  justify-content: space-between;
}
.flex-level {
  display: flex;
  gap:20px;
}

button:hover {
  /* background: red;
  color: white; */
  background: rgb(226, 17, 17);
  /* color: rgb(252, 92, 92); */
  color: black;
}

.card-grid {
  margin-top: 20px;
  display: grid;
  /* grid-template-columns: 1fr 1fr 1fr 1fr; */
  grid-template-columns: repeat(4,1fr);
  grid-gap: 20px;
}

button:disabled {
  background-color: rgb(245, 145, 145);
  color: gray
}

button:disabled:hover {
  cursor: default;
}
