.card {
  position: relative;
}

.card:hover {
  opacity:0.7;
}

.card img {
  width: 100%;
  /* height: 100%; */
  display: block;
  border: 2px solid white;
  border-radius: 6px;
}

/* front of the card */
.card .front {
  transform: rotateY(90deg);
  position: absolute;
  transition: all ease-in 0.2s;
}

.flipped .front {
 transform: rotateY(0deg);
 transition-delay: 0.2s; 
}

/*back of the card */
.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}

.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;
}